.wrapper {
    max-width: 800px;
    margin: 0 auto;
    margin-bottom: 200px;
  }
  
  .inputs {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
    margin-bottom: 30px;
  }
  
  .input {
    height: 50px;
    border-radius: 10px;
    border: none;
    padding: 0 20px;
    font-size: 16px;
  }
  
  .textareas {
    display: grid;
    grid-template-columns: 4fr 1fr;
    gap: 30px;
  }
  
  .textarea {
    min-height: 150px;
    max-width: 740px;
    min-width: 200px;
    border-radius: 10px;
    border: none;
    padding: 20px;
    font-size: 16px;
  }
  
  .send {
    border: none;
    font-size: 16px;
  }
  
  .send:hover {
    cursor: pointer;
  }
  
  .icon {
    width: 30px;
    height: 30px;
  }
  
  @media only screen and (max-width: 560px) {
    .inputs {
      grid-template-columns: 1fr;
    }
  
    .textareas {
      grid-template-columns: 1fr;
    }
  
    .send {
      min-height: 50px;
    }
  }